import { mapState, mapMutations } from 'vuex';

export default {
    props: {
        imgSrc: String,
        categoryList: Array,
        classPrefix: String,
    },
    data: function () {
        return {
            loading: false,
            fetching: false,
            data: [],
            value: [],
            playing: false,
            categoryListDuplicate: this.categoryList,
        };
    },
    computed: {
        ...mapState(['postsDetail']),
    },
    methods: {
        ...mapMutations(['searchArticle']),
        fetchPosts(value) {
            this.data = [];
            this.fetching = true;
            return new Promise((resolve, reject) => {
                setTimeout(() => {
                    this.fetching = false;
                    const data = this.postsDetail.map(({ title }) =>
                        title.toLowerCase().indexOf(value.toLowerCase()) < 0
                            ? undefined
                            : {
                                  text: title,
                                  value: title,
                              }
                    );
                    this.data = data.filter(Boolean);
                    resolve(this.postsDetail);
                }, 100);
            });
        },
        handleChange(value) {
            console.log(value, 'value')
            this.$store.commit('searchArticle', { title: value.key });
            Object.assign(this, { value });
        },
        play() {
            this.audioEle.play();
            this.playing = true;
        },
        pause() {
            this.audioEle.pause();
            this.playing = false;
        }
    },
    mounted: function() {
        this.audioEle = document.getElementById('header-music');
    }
};
